import { useEffect, useState } from 'react';

import './App.css';
import { appid, cityList } from './data/data';

// Components
import { WeatherNow } from './components/weatherNow/weatherNow';
import { WeatherFuture } from './components/weatherFuture/weatherFuture';

const initialCity = cityList[0];
const initialWeatherNowData = await fetch(`https://api.openweathermap.org/data/2.5/weather?id=${initialCity.id}&appid=${appid}&lang=ua&units=metric`).then(response => response.json());
const initialWeatherFutureData = await fetch(`https://api.openweathermap.org/data/2.5/forecast?id=${initialCity.id}&appid=${appid}&lang=ua&units=metric`).then(response => response.json());

function App() {

  const [city, setCity] = useState(initialCity);
  const [searcherValue, setSearcherValue] = useState('');
  const [filteredCityList, getFilteredCityList] = useState([]);
  const [options, setOptions] = useState({
    lang: 'ua',
    units: 'metric',
    unitsTitle: 'C'
  })

  const [weatherNowData, setWeatherNowData] = useState(initialWeatherNowData);
  const [weatherFutureData, setWeatherFutureData] = useState(initialWeatherFutureData);

  //            
  useEffect(
    () => {
      getFilteredCityList(cityList.filter(city => city.name_ua.includes(searcherValue) && searcherValue != ''));
    }, [searcherValue]
  );

  //               
  function changeCity(e) {
    setCity(cityList.filter(city => city.id == e.target.dataset.cityId)[0]);
    setSearcherValue('');
  };

  //           
  useEffect(
    () => {
      const url = `https://api.openweathermap.org/data/2.5/weather?id=${city.id}&appid=${appid}&lang=${options.lang}&units=${options.units}`;
      fetch(url).then(response => response.json()).then(json => setWeatherNowData(json));

      const urlFuture = `https://api.openweathermap.org/data/2.5/forecast?id=${city.id}&appid=${appid}&lang=${options.lang}&units=${options.units}`;
      fetch(urlFuture).then(response => response.json()).then(json => setWeatherFutureData(json));
    }, [city]
  );

  return (
    <div className='container'>
      <div className="city-time">
        <div className="city">
          <span className='current-city'>{city.name_ua}</span>
          <input type="text" className="city-searcher" value={searcherValue} onInput={(e) => setSearcherValue(e.target.value)} />
          <ul className="city-output">
            {filteredCityList.map(city => <li onClick={changeCity} key={city.id} data-city-id={city.id}>{city.name_ua}</li>)}
          </ul>
        </div>
        <div className="current-date">
          {new Date(weatherNowData.dt * 1000).toLocaleDateString()} {new Date(weatherNowData.dt * 1000).toLocaleTimeString().slice(0, -3)}
        </div>
      </div>

      <WeatherNow weatherNowData={weatherNowData} unitsTitle={options.unitsTitle}/>
      <WeatherFuture weatherFutureData={weatherFutureData} unitsTitle={options.unitsTitle}/>

    </div>
  );
}

export default App;
